import LocalizedStrings from "react-localization";
import { makeAutoObservable } from "mobx";
import { IconMoon } from "@consta/icons/IconMoon";
class Localization {
	items = ["ru", "en"];
	language = "ru";

	constructor() {
		makeAutoObservable(this);
		this.setLanguage();
	}

	strings = new LocalizedStrings({
		en: {
			TodoTitle: "Enter title",
			TodoDate: "DD/MM/YYYY",
			ModalLogOut: "Log out",
			ModalLogIn: "Log in/Sign up",
			Email: "Enter email",
			Password: "Enter password",
			Username: "Enter username",
			Create: "Create",
			Change: "Change",
			ChangeTodo: "Change todo",
			CreateTodo: "Create todo",
			LogIn: "Log in",
			SignUp: "Sign up",
			TodoList: "You have no todos",
			ProvideData: "Provide data",
			ProvideRealEmail: "Provide real email",
			SignUpSuccess: "Sign up success. Now log in",
			LogInFailed: "Log in failed",
			LogInSuccess: "Log in success",
			LogOut: "Successfully logged out",
			ChangeTodo: "You successfully changed a Todo",
			AddTodo: "You have created a Todo",
			FirstLogIn: "First log in",
			DeleteTodo: "You have deleted a Todo",
		},
		ru: {
			TodoTitle: "Введите название",
			TodoDate: "ДД/ММ/ГГГГ",
			ModalLogOut: "Выйти",
			ModalLogIn: "Войти/Зарегистрироваться",
			Email: "Введите почту",
			Password: "Введите пароль",
			Username: "Введите имя",
			Create: "Создать",
			Change: "Изменить",
			ChangeTodo: "Изменить запись",
			CreateTodo: "Создать запись",
			LogIn: "Войти",
			SignUp: "Зарегистрироваться",
			TodoList: "У вас нет записей",
			ProvideData: "Напишите данные",
			ProvideRealEmail: "Напишате настоящюю почту",
			SignUpSuccess: "Вы зарегестрировались. Войдите в аккаунт",
			LogInFailed: "Ошибка при входе",
			LogInSuccess: "Вы успешно вошли",
			LogOut: "Вы успешно вышли",
			ChangeTodo: "Вы изменили запись",
			AddTodo: "Вы создали запись",
			FirstLogIn: "Сначала войдите",
			DeleteTodo: "Вы удалили запись",
		},
	});

	setLanguage = () => {
		if (localStorage.getItem("Localization") == "ru") {
			this.strings.setLanguage("ru");
			this.language = "ru";
		} else {
			this.strings.setLanguage("en");
			this.language = "en";
		}
	};

	saveLanguage = (item) => {
		localStorage.setItem("Localization", item);
		this.strings.setLanguage(item);
		this.language = item;
	};
}

export default new Localization();
